@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');

body{
  font-family: 'Poppins', sans-serif;
  margin: 0;
  background: linear-gradient(to right, #EADEDB , #BC70A4 , #BFD641 );
}

.box{
  background-color: rgb(31, 30, 30);
  opacity: 0.9;
  color: white;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 3px 3px;
  width: 600px;
  max-width: 100%;
  text-align: center;
}

.box h2{
  margin-top: 0;
}

.box p{
  margin: 2rem;
}

.box button{
  border: 0;
  border-radius: 3px;
  box-shadow: 3px 3px;
  padding: 0.5rem 1rem;
  cursor: pointer;
}

.box button:active{
  outline: none;
  transform: scale(0.98);
}

#root{
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

